.contact {
  padding: 100px 0;
  background: var(--hero-gradient);
  position: relative;
  overflow: hidden;
  transition: background 0.3s ease;
}

[data-theme="light"] .contact {
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
}

.contact-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.floating-particles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.particle {
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  animation: floatParticle 20s linear infinite;
  transition: background 0.3s ease;
}

[data-theme="light"] .particle {
  background: rgba(0, 0, 0, 0.2);
}

.particle-1 {
  width: 4px;
  height: 4px;
  left: 10%;
  animation-delay: 0s;
}

.particle-2 {
  width: 6px;
  height: 6px;
  left: 20%;
  animation-delay: 2s;
}

.particle-3 {
  width: 3px;
  height: 3px;
  left: 30%;
  animation-delay: 4s;
}

.particle-4 {
  width: 5px;
  height: 5px;
  left: 40%;
  animation-delay: 6s;
}

.particle-5 {
  width: 4px;
  height: 4px;
  left: 50%;
  animation-delay: 8s;
}

.particle-6 {
  width: 7px;
  height: 7px;
  left: 60%;
  animation-delay: 10s;
}

.particle-7 {
  width: 3px;
  height: 3px;
  left: 70%;
  animation-delay: 12s;
}

.particle-8 {
  width: 5px;
  height: 5px;
  left: 80%;
  animation-delay: 14s;
}

.particle-9 {
  width: 4px;
  height: 4px;
  left: 90%;
  animation-delay: 16s;
}

.particle-10 {
  width: 6px;
  height: 6px;
  left: 15%;
  animation-delay: 18s;
}

.particle-11 {
  width: 3px;
  height: 3px;
  left: 25%;
  animation-delay: 1s;
}

.particle-12 {
  width: 5px;
  height: 5px;
  left: 35%;
  animation-delay: 3s;
}

.particle-13 {
  width: 4px;
  height: 4px;
  left: 45%;
  animation-delay: 5s;
}

.particle-14 {
  width: 7px;
  height: 7px;
  left: 55%;
  animation-delay: 7s;
}

.particle-15 {
  width: 3px;
  height: 3px;
  left: 65%;
  animation-delay: 9s;
}

.particle-16 {
  width: 5px;
  height: 5px;
  left: 75%;
  animation-delay: 11s;
}

.particle-17 {
  width: 4px;
  height: 4px;
  left: 85%;
  animation-delay: 13s;
}

.particle-18 {
  width: 6px;
  height: 6px;
  left: 95%;
  animation-delay: 15s;
}

.particle-19 {
  width: 3px;
  height: 3px;
  left: 5%;
  animation-delay: 17s;
}

.particle-20 {
  width: 5px;
  height: 5px;
  left: 95%;
  animation-delay: 19s;
}

@keyframes floatParticle {
  0% {
    transform: translateY(100vh) rotate(0deg);
    opacity: 0;
  }

  10% {
    opacity: 1;
  }

  90% {
    opacity: 1;
  }

  100% {
    transform: translateY(-100vh) rotate(360deg);
    opacity: 0;
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 5%;
  position: relative;
  z-index: 2;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
  color: white;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 600;
  font-family: 'Courier New', 'Monaco', monospace;
  color: white;
  margin-bottom: 1rem;
  animation: fadeInScale 1s ease-out;
  transition: color 0.3s ease;
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.8);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.title-underline {
  width: 60px;
  height: 4px;
  background: white;
  margin: 0 auto 2rem;
  border-radius: 2px;
  animation: expandWidth 1s ease-out 0.5s both;
}

@keyframes expandWidth {
  from {
    width: 0;
  }

  to {
    width: 60px;
  }
}

.section-subtitle {
  font-size: 1.1rem;
  font-family: 'Courier New', 'Monaco', monospace;
  color: var(--accent-purple);
  max-width: 600px;
  margin: 0 auto;
  line-height: 1.6;
  animation: fadeInUp 1s ease-out 0.3s both;
  transition: color 0.3s ease;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 4rem;
  align-items: start;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.info-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 2rem;
  border-radius: 12px;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.2);
  transition: all 0.3s ease;
  animation: slideInLeft 1s ease-out;
  animation-fill-mode: both;
}

[data-theme="light"] .info-card {
  background: rgb(241, 252, 247);
  border-color: rgb(141, 226, 195);
}

[data-theme="light"] .info-card h3 {
  color: var(--bg-primary);
}

.info-card:nth-child(1) {
  animation-delay: 0.1s;
}

.info-card:nth-child(2) {
  animation-delay: 0.2s;
}

.info-card:nth-child(3) {
  animation-delay: 0.3s;
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.info-card:hover {
  transform: translateY(-5px);
}

.info-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.2));
}

.info-card h3 {
  color: white;
  font-size: 1.3rem;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.info-card p {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
}

.social-links {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 2rem;
  border-radius: 20px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation: slideInLeft 1s ease-out 0.4s both;
}

[data-theme="light"] .social-links {
  background: rgb(241, 252, 247);
  border-color: rgb(141, 226, 195);
}

.social-links h3 {
  color: white;
  font-size: 1.3rem;
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 600;
}

[data-theme="light"] .social-links h3 {
  color: var(--bg-primary);
}

.social-icons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  border-radius: 10px;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

[data-theme="light"] .social-link {
  background: rgba(255, 255, 255, 0.15);
  color: var(--bg-primary);
  border-color: rgb(141, 226, 195);
}

.social-link:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

[data-theme="light"] .social-link:hover {
  background: rgb(203, 255, 235);
}

.contact-form-container {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  padding: 3rem;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  animation: slideInRight 1s ease-out;
  transition: all 0.3s ease;
}

[data-theme="light"] .contact-form-container {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.95) 0%, rgba(248, 249, 250, 0.9) 100%);
  border-color: rgba(102, 126, 234, 0.2);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.08);
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-group {
  position: relative;
}

.form-input {
  width: 100%;
  padding: 1.2rem 1rem 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 1rem;
  font-family: 'Courier New', 'Monaco', monospace;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
}

[data-theme="light"] .form-input {
  color: #667eea;
  background: rgba(255, 255, 255, 0.15);
  border-color: rgb(141, 226, 195);
}

.form-input:focus {
  outline: none;
  border-color: rgba(255, 255, 255, 0.5);
  background: rgba(255, 255, 255, 0.15);
}

[data-theme="light"] .form-input:focus {
  border-color: rgb(141, 226, 195);
  background: rgba(255, 255, 255, 0.25);
}

.form-input::placeholder {
  color: transparent;
}

.form-label {
  position: absolute;
  top: 1rem;
  left: 1rem;
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.3s ease;
  pointer-events: none;
  font-size: 1rem;
}

[data-theme="light"] .form-label {
  color: rgba(102, 126, 234, 0.7);
}

.form-input:focus+.form-label,
.form-input:not(:placeholder-shown)+.form-label {
  top: 0.3rem;
  font-size: 0.8rem;
  color: white;
}

[data-theme="light"] .form-input:focus+.form-label,
[data-theme="light"] .form-input:not(:placeholder-shown)+.form-label {
  color: rgba(43, 76, 223, 0.7);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  padding: 1.2rem 2rem;
  background: rgba(255, 255, 255, 0.9);
  color: var(--bg-primary);
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: 600;
  font-family: 'Courier New', 'Monaco', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

[data-theme="light"] .submit-btn {
  background: rgba(150, 213, 255, 0.95);
}

.submit-btn:hover:not(:disabled) {
  background: white;
  transform: translateY(-3px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.submit-btn.submitting {
  pointer-events: none;
}

.spinner {
  width: 20px;
  height: 20px;
  border: 2px solid #667eea;
  border-top: 2px solid transparent;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 768px) {
  .contact {
    padding: 60px 0;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .contact-form-container {
    padding: 2rem;
  }

  .social-icons {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .contact-form-container {
    padding: 1.5rem;
  }

  .form-input {
    padding: 1rem 0.8rem 0.4rem;
  }

  .form-label {
    left: 0.8rem;
  }
}